.modal {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    z-index: -99;
    background-color: rgba(0, 0, 0, 0);
    transition: z-index 0.2s linear, background-color 0.2s linear;
  }
  
  .modal.active {
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    transition: z-index 0.2s linear, background-color 0.2s linear;
  }
  